import Vue from 'vue';
import axios from 'axios';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faShoppingCart, faTrashAlt, faUser } from '@fortawesome/free-solid-svg-icons';
import { faCheckCircle } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import Snotify from 'vue-snotify';
import 'vue-snotify/styles/material.css';
import {
  ValidationObserver, ValidationProvider, extend, localize,
} from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_TW.json';
import * as rules from 'vee-validate/dist/rules';

import App from './App.vue';
import router from './router';
import store from './store';
import currencyFilter from './filters/currency';
import dateFilter from './filters/date';

Vue.config.productionTip = false;
Vue.prototype.axios = axios;
// 將 cookie 的開關打開
axios.defaults.withCredentials = true;

Vue.component('Loading', Loading);
Vue.component('ValidationObserver', ValidationObserver);
Vue.component('ValidationProvider', ValidationProvider);

// add fortawesome library
library.add(faShoppingCart, faTrashAlt, faCheckCircle, faUser);
Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.filter('currency', currencyFilter);
Vue.filter('date', dateFilter);

Object.keys(rules).forEach((rule) => {
  extend(rule, rules[rule]);
});
localize('zh_TW', zhTW);

const options = {
  toast: {
    showProgressBar: false,
    pauseOnHover: false,
  },
};
Vue.use(Snotify, options);

new Vue({
  router,
  store,
  beforeCreate() {
    Vue.$snotify = this.$snotify;
  },
  render: (h) => h(App),
}).$mount('#app');

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    // 這裡需要驗證
    // 檢查用戶是否仍持續登入
    const url = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    axios.post(url).then((response) => {
      if (response.data.success) {
        // 已經登入
        next();
      } else {
        // 尚未登入導到login page
        next({
          path: '/login',
        });
      }
    });
  } else {
    next();
  }
});
