import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Home,
    children: [
      {
        path: '',
        name: 'Shop',
        component: () => import('../views/Shop.vue'),
      },
      {
        path: '/japantv',
        name: 'JapanTv',
        component: () => import('../views/JapanTv.vue'),
      },
      {
        path: '/koreatv',
        name: 'KoreaTv',
        component: () => import('../views/KoreaTv.vue'),
      },
      {
        path: 'product/:productId',
        name: 'Product',
        component: () => import('../views/Product.vue'),
        props: true,
      },
      {
        path: 'cart',
        name: 'Cart',
        component: () => import('../views/Cart.vue'),
      },
      {
        path: 'order',
        name: 'Order',
        component: () => import('../views/Order.vue'),
      },
      {
        path: 'confirm_payment/:orderId',
        name: 'ConfirmPayment',
        component: () => import('../views/ConfirmPayment.vue'),
        props: true,
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue'),
  },
  {
    path: '/admin',
    name: 'Dashboard',
    component: () => import('../views/Dashboard.vue'),
    redirect: { name: 'DashboardProducts' },
    children: [
      {
        path: 'products',
        name: 'DashboardProducts',
        component: () => import('../views/DashboardProducts.vue'),
        meta: { requiresAuth: true },
      },
      {
        path: 'order_list',
        name: 'DashboardOrderList',
        component: () => import('../views/DashboardOrderList.vue'),
        meta: { requiresAuth: true },
      },
      {
        path: 'coupon',
        name: 'DashboardCoupon',
        component: () => import('../views/DashboardCoupon.vue'),
        meta: { requiresAuth: true },
      },
    ],
  },
  {
    // 如果輸入不是router 路徑 ,導到 /
    path: '*',
    redirect: '/',
  },
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    return { x: 0, y: 0 };
  },
});

export default router;
