import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLoading: false,
    allTvs: [],
    product: {},
    cartData: {},
    couponCode: '',
    order: {},
  },
  mutations: {
    LOADING(state, loading) {
      state.isLoading = loading;
    },
    SET_ALL_TV(state, tvs) {
      state.allTvs = tvs;
    },
    SET_TV(state, product) {
      state.product = product;
    },
    SET_CART(state, cart) {
      state.cartData = cart;
    },
    SET_COUPONCODE(state, value) {
      state.couponCode = value;
    },
    SET_ORDER(state, order) {
      state.order = order;
    },
  },
  actions: {
    getAllProducts({ commit }) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;
      commit('LOADING', true);
      axios.get(url).then((response) => {
        commit('LOADING', false);
        commit('SET_ALL_TV', response.data.products);
      });
    },
    getProduct({ commit, state }, id) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      commit('SET_TV', {});
      commit('LOADING', true);
      axios.get(url).then((response) => {
        commit('SET_TV', response.data.product);
        Vue.set(state.product, 'num', 1);
        commit('LOADING', false);
      });
    },
    getCar({ commit }) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      axios.get(url).then((response) => {
        commit('SET_CART', response.data.data);
      });
    },
    removeCart({ commit, dispatch }, id) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      commit('LOADING', true);
      axios.delete(url).then(() => {
        commit('LOADING', false);
        dispatch('getCar');
      });
    },
    addtoCart({ commit, dispatch }, { id, nums }) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const cart = {
        product_id: id,
        qty: nums,
      };
      commit('LOADING', true);
      axios.post(url, { data: cart }).then(() => {
        commit('LOADING', false);
        dispatch('getCar');
        Vue.$snotify.success('已加入購物車');
      });
    },
    addCouponCode({ commit, dispatch, state }) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      const coupon = {
        code: state.couponCode,
      };
      axios.post(url, { data: coupon }).then((response) => {
        const couponMessage = response.data.message;
        if (response.data.success) {
          Vue.$snotify.success(couponMessage);
          dispatch('getCar');
        } else {
          Vue.$snotify.warning(couponMessage);
        }
        commit('SET_COUPONCODE', '');
      });
    },
    getOrder({ commit }, orderId) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${orderId}`;
      commit('SET_ORDER', {});
      commit('LOADING', true);
      axios.get(url).then((response) => {
        commit('SET_ORDER', response.data.order);
        commit('LOADING', false);
      });
    },
    payOrder({ commit, dispatch }, orderId) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${orderId}`;
      commit('LOADING', true);
      axios.post(url).then((response) => {
        if (response.data.success) {
          window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth',
          });
          dispatch('getOrder', orderId);
          dispatch('getCar');
          Vue.$snotify.success(response.data.message);
          commit('LOADING', false);
        }
      });
    },
  },
  modules: {
  },
  getters: {
    reverseTvs(state) {
      return state.allTvs.slice().reverse();
    },
    japanTv(state, getters) {
      return getters.reverseTvs.filter((item) => item.category === '經典日劇');
    },
    koreaTv(state, getters) {
      return getters.reverseTvs.filter((item) => item.category === '口碑韓劇');
    },
    cartLength(state) {
      if (state.cartData.carts) {
        return state.cartData.carts.length;
      }
      return 0;
    },
  },
});
